const header = document.querySelectorAll('#header')
console.log(header)
const $linkWarp = $('.linkWarp')
const $last = $linkWarp.find('li.last')
const x = localStorage.getItem('x')
const xObject = JSON.parse(x)
console.log(x)
console.log(xObject)
const hashMap = xObject || [
   { logo:'A',url:'https://www.acfun.cn'},
   { logo:'b',url:'https://bilibili.com'},
   { logo:'c',url:'https://caobao.com'},
]

const remove = (url) => {
   return url.replace('http://', '')
      .replace('https://', '')
      .replace('wwww.', '')
      .replace(/\/.*/,'') //Âà†Èô§‰ª•/ÂºÄÂ§¥ÁöÑÂêéÈù¢ÊâÄÊúâÂÜÖÂÆπ  
}

const render = (x) => {
   $linkWarp.find('li:not(.last)').remove()  //ÂÖàÂà†Èô§‰πãÂâçÁöÑ ÂÜçÊ∑ªÂä†
   hashMap.forEach((node, index) => {
      // console.log(index) //ÊãøÂà∞È°µÈù¢Êï∞ÊçÆÁöÑÁ¥¢Âºï
      const $li = $(`<li>
         <a href="${node.url}">
            <div class="listBox">
               <div class="logo">${node.logo}</div>
               <div class="link">${remove(node.url)}</div>
            </div>
         </a>
         <span class= 'close'>
            <svg class="icon" >
               <use xlink:href="#icon-guanbi1"></use>
            </svg>
         </span>
      </li>`).insertBefore($last)
   
      $li.on('click', '.close', () => {
         // hashMap.splice(index, 1)
         console.log(hashMap)
         hashMap.splice(index, 1)
         render()
      })
   })

   
}


render()
$('.addBox').on('click', () => {
   let url = window.prompt('ËØ∑ËæìÂÖ•‰Ω†ÊÉ≥Ë¶ÅÁöÑÁΩëÂùÄ')
   if (url.indexOf('http') !== 0) { //Â¶ÇÊûúÂÆÉ‰∏çÊòØhttpÂºÄÂ§¥ÁöÑ
      url = 'https://'+url
   }
   console.log(url)
   hashMap.push({
      logo: remove(url)[0],
      url: url
   })
   render()

})

window.onbeforeunload = () => {
   const string = JSON.stringify(hashMap)
   localStorage.setItem('x',string)
}

$('#mainWarp').on('keypress', (e) => {
   // console.log(e.key) //ÊâæÂà∞Áî®Êà∑ÊåâÁöÑÊòØÂì™‰∏™ÈîÆ 
   const {key} = e //Áõ∏ÂΩì‰∫éconst key = key.e
   for (let i = 0; i < hashMap.length; i++){
      if (hashMap[i].logo.toLowerCase() === key){
         window.open(hashMap[i].url)
      }
   }
})

